import React, { Component } from "react";
import { connect } from "react-redux";
import ListadoObjetos from "./ListadoObjetos";
import { searchChamps } from "../actions/champsActions";
import { Link } from "react-router-dom";
import CuadrosBuild from "./CuadrosBuild";

export class CrearBuilds extends Component {
  state = {
    inicial: [],
    inicialSupport: [],
    build: [],
    buildSupport: [],
    actual: "inicial"
  };

  selectClass(select) {
    this.props.searchChamps({ select, search: "" });
  }

  resetear() {
    this.setState({
      inicial: [],
      inicialSupport: [],
      build: [],
      buildSupport: [],
      actual: "inicial"
    });
  }

  generar() {
    const { inicial, inicialSupport, build, buildSupport } = this.state;
    console.log(`${(inicial[0], inicial[1], inicial[2])}`);
    var generado = `{
      "champ": "${this.props.match.params.champ}",
      "inicial": ["${inicial[0]}" , "${inicial[1]}", "${inicial[2]}"],
      "inicialSupport": ["${inicialSupport[0]}",
      "${inicialSupport[1]}",
      "${inicialSupport[2]}"],
      "build": ["${build[0]}", "${build[1]}", "${build[2]}","${build[3]}",
    "${build[4]}",
    "${build[5]}"],
      "buildSupport": ["${buildSupport[0]}",
      "${buildSupport[1]}",
      "${buildSupport[2]}","${buildSupport[3]}", "${buildSupport[4]}", "${
      buildSupport[5]
    }"]
    },`;

    this.setState({
      generado
    });
  }

  addItem(icono) {
    const { actual, inicial, inicialSupport, build, buildSupport } = this.state;

    if (actual != "") {
      if (actual == "inicial" && inicial.length < 3) {
        this.setState({
          inicial: [...inicial, icono]
        });
      } else if (actual == "inicialSupport" && inicialSupport.length < 3) {
        this.setState({
          inicialSupport: [...inicialSupport, icono]
        });
      } else if (actual == "build" && build.length < 6) {
        this.setState({
          build: [...build, icono]
        });
      } else if (actual == "buildSupport" && buildSupport.length < 6) {
        this.setState({
          buildSupport: [...buildSupport, icono]
        });
      }

      if (actual == "inicial" && inicial.length == 3) {
        this.setState({
          actual: "inicialSupport",
          inicialSupport: [...inicialSupport, icono]
        });
      }
      if (actual == "inicialSupport" && inicialSupport.length == 3) {
        this.setState({
          actual: "build",
          build: [...build, icono]
        });
      }
      if (actual == "build" && build.length == 6) {
        this.setState({
          actual: "buildSupport",
          buildSupport: [...buildSupport, icono]
        });
      }
      if (actual == "buildSupport" && buildSupport.length == 6) {
        this.setState({
          actual: ""
        });
      }
    }
  }

  changeActual(actual) {
    this.setState({
      actual
    });
  }

  deleteItem(tipo, posicion) {
    this.setState({
      [tipo]: this.state[tipo].filter((valor, i) => {
        return i != posicion;
      })
    });
  }

  render() {
    const id = this.props.match.params.champ;
    const { champs } = this.props;
    var data = {};
    var newTags = [];
    // busco el dato
    champs.forEach(champ => {
      if (champ.id == id) {
        data = champ;
      }
    });
    const { name, icon, tags } = data;
    const {
      inicial,
      inicialSupport,
      build,
      buildSupport,
      generado
    } = this.state;

    // cambio las tags
    tags.forEach(tag => {
      switch (tag) {
        case "Fighter":
          newTags.push("Luchador");
          break;
        case "Tank":
          newTags.push("Tanque");
          break;
        case "Mage":
          newTags.push("Mago");
          break;
        case "Assassin":
          newTags.push("Asesino");
          break;
        case "Support":
          newTags.push("Soporte");
          break;
        case "Marksman":
          newTags.push("Adc (Tirador)");
          break;

        default:
          break;
      }
    });

    return (
      <div className="creador">
        <div className="container-fluid">
          <div className="container-creador">
            <div className="header">
              <h2>Crear Builds</h2>
            </div>
            <div className="row">
              <div className="col-md-6">
                <div className="champData">
                  <div className="image">
                    <img src={icon} alt={name} />
                  </div>
                  <div className="datos">
                    <h2>{name}</h2>
                    <div className="tags">
                      {tags.map((tag, index) => {
                        return (
                          <Link
                            to="/"
                            onClick={this.selectClass.bind(this, tag)}
                          >
                            {newTags[index]}{" "}
                          </Link>
                        );
                      })}
                    </div>
                  </div>
                </div>

                <div className="build">
                  <h4>Objetos iniciales:</h4>
                  <div className="cuadrosBuild">
                    <CuadrosBuild
                      valor={inicial[0]}
                      delete={this.deleteItem.bind(this, "inicial", 0)}
                    />
                    <CuadrosBuild
                      valor={inicial[1]}
                      delete={this.deleteItem.bind(this, "inicial", 1)}
                    />
                    <CuadrosBuild
                      valor={inicial[2]}
                      delete={this.deleteItem.bind(this, "inicial", 2)}
                    />
                  </div>
                  <h4>Objetos iniciales (Support):</h4>
                  <div className="cuadrosBuild">
                    <CuadrosBuild
                      valor={inicialSupport[0]}
                      delete={this.deleteItem.bind(this, "inicialSupport", 0)}
                    />
                    <CuadrosBuild
                      valor={inicialSupport[1]}
                      delete={this.deleteItem.bind(this, "inicialSupport", 1)}
                    />
                    <CuadrosBuild
                      valor={inicialSupport[2]}
                      delete={this.deleteItem.bind(this, "inicialSupport", 1)}
                    />
                  </div>
                  <h4>Build:</h4>
                  <div className="cuadrosBuild">
                    <CuadrosBuild
                      valor={build[0]}
                      delete={this.deleteItem.bind(this, "build", 0)}
                    />
                    <CuadrosBuild
                      valor={build[1]}
                      delete={this.deleteItem.bind(this, "build", 1)}
                    />
                    <CuadrosBuild
                      valor={build[2]}
                      delete={this.deleteItem.bind(this, "build", 2)}
                    />
                    <CuadrosBuild
                      valor={build[3]}
                      delete={this.deleteItem.bind(this, "build", 3)}
                    />
                    <CuadrosBuild
                      valor={build[4]}
                      delete={this.deleteItem.bind(this, "build", 4)}
                    />
                    <CuadrosBuild
                      valor={build[5]}
                      delete={this.deleteItem.bind(this, "build", 5)}
                    />
                  </div>
                  <h4>Build (Support):</h4>
                  <div className="cuadrosBuild">
                    <CuadrosBuild
                      valor={buildSupport[0]}
                      delete={this.deleteItem.bind(this, "buildSupport", 0)}
                    />
                    <CuadrosBuild
                      valor={buildSupport[1]}
                      delete={this.deleteItem.bind(this, "buildSupport", 1)}
                    />
                    <CuadrosBuild
                      valor={buildSupport[2]}
                      delete={this.deleteItem.bind(this, "buildSupport", 2)}
                    />
                    <CuadrosBuild
                      valor={buildSupport[3]}
                      delete={this.deleteItem.bind(this, "buildSupport", 3)}
                    />
                    <CuadrosBuild
                      valor={buildSupport[4]}
                      delete={this.deleteItem.bind(this, "buildSupport", 4)}
                    />
                    <CuadrosBuild
                      valor={buildSupport[5]}
                      delete={this.deleteItem.bind(this, "buildSupport", 5)}
                    />
                  </div>
                  <button onClick={this.generar.bind(this)}>
                    Generar código
                  </button>
                  <button onClick={this.resetear.bind(this)}>Resetear</button>
                </div>
              </div>
              <div className="col-md-6">
                <ListadoObjetos add={this.addItem.bind(this)} />
              </div>
            </div>
            <div className="resultado">{generado}</div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  champs: state.champs.champs,
  objetos: state.champs.objetos
});

export default connect(
  mapStateToProps,
  { searchChamps }
)(CrearBuilds);
