import React, { Component } from "react";

export class CuadrosBuild extends Component {
  render() {
    const { valor } = this.props;
    return (
      <div className="cuadroBuild" onClick={this.props.delete.bind(this)}>
        {valor != undefined ? <img src={valor} /> : null}
      </div>
    );
  }
}

export default CuadrosBuild;
