import React, { Component } from "react";
import Objeto from "./Objeto";
import { connect } from "react-redux";

export class ListadoObjetos extends Component {
  state = {
    dmg: false,
    ini: false,
    mag: false,
    def: false,
    bot: false
  };

  toggle(e) {
    var seccion = e.target.id;
    this.setState({
      [seccion]: !this.state[seccion]
    });
  }

  render() {
    const { objetos } = this.props;
    const { dmg, ini, mag, def, bot } = this.state;

    return (
      <div className="objetos">
        <h3>Listado de objetos</h3>
        <div className="seccion">
          <h2 id="ini" onClick={this.toggle.bind(this)}>
            Inicio
          </h2>
          <div className={`row ${ini ? "" : "d-none"}`}>
            {objetos.map(objeto => {
              if (objeto.type == "Inicio") {
                return (
                  <Objeto
                    add={this.props.add.bind(this)}
                    key={objeto.name}
                    data={objeto}
                  />
                );
              }
            })}
          </div>
        </div>
        <div className="seccion">
          <h2 id="dmg" onClick={this.toggle.bind(this)}>
            Daño
          </h2>
          <div className={`row ${dmg ? "" : "d-none"}`}>
            {objetos.map(objeto => {
              if (objeto.type == "Daño") {
                return (
                  <Objeto
                    add={this.props.add.bind(this)}
                    key={objeto.name}
                    data={objeto}
                  />
                );
              }
            })}
          </div>
        </div>
        <div className="seccion">
          <h2 id="mag" onClick={this.toggle.bind(this)}>
            Magia
          </h2>
          <div className={`row ${mag ? "" : "d-none"}`}>
            {objetos.map(objeto => {
              if (objeto.type == "Magia") {
                return (
                  <Objeto
                    add={this.props.add.bind(this)}
                    key={objeto.name}
                    data={objeto}
                  />
                );
              }
            })}
          </div>
        </div>
        <div className="seccion">
          <h2 id="def" onClick={this.toggle.bind(this)}>
            Defensa
          </h2>
          <div className={`row ${def ? "" : "d-none"}`}>
            {objetos.map(objeto => {
              if (objeto.type == "Defensa") {
                return (
                  <Objeto
                    add={this.props.add.bind(this)}
                    key={objeto.name}
                    data={objeto}
                  />
                );
              }
            })}
          </div>
        </div>
        <div className="seccion">
          <h2 id="bot" onClick={this.toggle.bind(this)}>
            Botas
          </h2>
          <div className={`row ${bot ? "" : "d-none"}`}>
            {objetos.map(objeto => {
              if (objeto.type == "Botas") {
                return (
                  <Objeto
                    add={this.props.add.bind(this)}
                    key={objeto.name}
                    data={objeto}
                  />
                );
              }
            })}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  objetos: state.champs.objetos
});

export default connect(
  mapStateToProps,
  null
)(ListadoObjetos);
